body{font-size: 18px;line-height: 140%;color: rgba(0, 0, 0, 0.8);font-family: 'GraphikLCG-Regular',  Arial, sans-serif;}
.popular_block_price span, .tarif_price span, .tarif_old_price span, .add_note{font-family: 'GraphikLCG-Regular',  Arial, sans-serif;}
.hamburger_label, .form_btn, .zakaz_btn, .services_block_overlay span, .more, .morebtn, input[type="submit"], .steps_title, .steps_iteration, .question, .tabs_title, h3, .add_table, .social a,
.footer a[href^=tel], .footer_text p:first-child, b, strong, .tab__item > span, .react-tabs__tab{font-family: 'GraphikLCG-Medium',  Arial, sans-serif;}
.top_phones_numbers, h2, .popular_block_title, .popular_block_price, .advantages_block_title, .reviews_block_title span, .services_block_title, .sale_text a[href^=tel], #sale h3,
.question_text a[href^=tel], .tarif_block_title, .tarif_price, .tarif_old_price, .feedback a, .slider_text h1, .slider_text h2{font-family: 'GraphikLCG-Semibold',  Arial, sans-serif;}
.display-none{display:none;}
img{display:inline-block;max-width:100%;height:auto;}
table{width:100%;}
a:hover, a:focus{text-decoration:none;}
input[type="text"], textarea, select{font-size: 18px;color:#000;border: 2px solid rgba(0, 0, 0, 0.15);background:#fff;border-radius:30px;width:100%;box-sizing:border-box;padding:15px 28px;margin-bottom:20px;}
input[type="text"]{height:60px;}
textarea{resize:none;height:120px;padding-top:15px;}
input[type="text"].required, textarea.error, select.error, #sale input[type="text"].required, input[type="text"].wrong-field, input[type="text"].wrong-field:focus{background: rgba(237, 55, 45, 0.03);border-color:#ED372D;}
#sale input[type="text"].wrong-field{background:#FEF9F8;border-color:#ED372D;}
input[type="text"].error:focus, textarea.error:focus, select.error:focus{background:#fff;}
input[type="text"]:focus, textarea:focus, select:focus, #sale input[type="text"]:focus{border-color:#FED34B;}
::-webkit-input-placeholder{font-size:18px;color:#000;font-family: 'GraphikLCG-Regular',  Arial, sans-serif;}
:-moz-placeholder{font-size:18px;color:#000;font-family: 'GraphikLCG-Regular',  Arial, sans-serif;}
::-moz-placeholder{font-size:18px;color:#000;font-family: 'GraphikLCG-Regular',  Arial, sans-serif;}
:-ms-input-placeholder{font-size:18px;color:#000;font-family: 'GraphikLCG-Regular',  Arial, sans-serif;}
input[type="submit"]{font-size: 18px;display:block;width:100%;background: #FED34B;padding:15px;border:none;border-radius: 30px;}
