	.tab{position: relative;display: flex;height: auto;}
	#tabs .react-tabs__tab{padding:22px 38px;}
	.tab__item>span{padding:22px 38px;cursor:pointer;position:relative;box-sizing:border-box;z-index:2;display:block;border-top:2px solid transparent;border-left:1px solid transparent;border-right:1px solid transparent;}
	.tab__item > span.active ~ .tab__content{z-index: 1;opacity: 1;}
	.tab__content{display:none;position:absolute;opacity:0;top:0;left:0;padding-top:70px;box-sizing:border-box;}
	.tab__content_inner{padding:50px 65px;background: #FFFFFF;}
