	.tabs_title:after, .tab__item>span:after{display:block;content:"";width:16px;height:16px;background:url(./images/arrow.png) no-repeat 50% 50%;position:absolute;right:20px;top:14px;}
	.active .tabs_title:after, .tab__item>span.active:after{transform: rotate(-180deg);}
	.top_menu{padding:60px 20px;}
	.tarif_block{margin-bottom:30px;}
	#tarifs_wrap .row .col-12:last-child .tarif_block{margin-bottom:0;}
}
